<template>
  <div id="bug_release">
    <div class="topOpt">
      <el-button type="primary" size="mini" @click="addBug">新增</el-button>
      <el-button type="primary" size="mini" @click="editBug">编辑</el-button>
      <el-button type="primary" size="mini" @click="deleteBug">删除</el-button>
      <el-input
        style="width:200px;"
        size="mini"
        @change="search"
        v-model="searchVal"
        placeholder="请输入任意字段搜索"
      ></el-input>
    </div>
    <div class="tablebox">
      <table>
        <thead>
          <tr>
            <th>
              <div></div>
            </th>
            <th>
              <div>序号</div>
            </th>
            <th>
              <div>提报人</div>
            </th>
            <th>
              <div>项目名称</div>
            </th>
            <th>
              <div>页面名称</div>
            </th>
            <th>
              <div>系统问题描述</div>
            </th>
            <th>
              <div>预计完成时间</div>
            </th>
            <th>
              <div>IT部确认时间</div>
            </th>
            <th>
              <div>状态</div>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in bugTable" :key="index">
            <td>
              <div>
                <input type="checkbox" :value="item.id" />
              </div>
            </td>
            <td>
              <div>{{ index + 1 }}</div>
            </td>
            <td>
              <!-- 提报人 -->
              {{ item.initiator }}
            </td>
            <td>
              <div>{{ item.project_name }}</div>
            </td>
            <td>
              <div>{{ item.page_name }}</div>
            </td>
            <td>
              <div class="detailMsg" @click="showBugDetail(item)">
                {{ item.description }}
              </div>
            </td>
            <td>
              <div>
                <!-- 预计完成时间 -->
                {{ item.expect_time }}
              </div>
              <!-- <div class="showImg" @click="showBigImg(item.bug_picture)">
                点击查看
              </div> -->
            </td>
            <td>
              <!-- 确认时间 -->
              <div>{{ item.it_time }}</div>
            </td>
            <td>
              <div v-if="item.data_status !== '已驳回'">
                {{ item.data_status }}
              </div>
              <div @click="showTurnDown(item)" v-else>
                {{ item.data_status }}
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <el-dialog
        title="新增系统反馈"
        :visible.sync="addDialogVisible"
        width="80%"
        :before-close="cancelAddForm"
      >
        <div class="addForm">
          <div class="form_item">
            <p>项目名称</p>
            <!-- <el-input v-model="addForm.sys_name"></el-input> -->
            <el-select @change="changePage" v-model="addForm.project_name">
              <el-option
                v-for="(item, index) in sys_names"
                :label="item"
                :value="item"
                :key="index"
                >{{ item }}</el-option
              >
            </el-select>
          </div>
          <div class="form_item">
            <p>页面名称</p>
            <el-select v-model="addForm.page_name">
              <el-option
                v-for="(item, index) in page_names"
                :label="item"
                :value="item"
                :key="index"
                >{{ item }}</el-option
              >
            </el-select>
          </div>
          <div class="form_item">
            <p>提报人</p>
            <el-input
              readonly
              placeholder="请输入提报人"
              v-model="addForm.initiator"
            ></el-input>
          </div>
          <!-- <div class="form_item">
            <p>问题类型</p>
            <el-select v-model="addForm.need_type">
              <el-option label="Bug" value="Bug"></el-option>
              <el-option label="需求" value="需求"></el-option>
            </el-select>
          </div> -->
          <div class="form_item totalBlock">
            <p>系统问题</p>
            <el-input
              type="textarea"
              :rows="8"
              placeholder="请输入系统问题"
              v-model="addForm.description"
            ></el-input>
          </div>
          <div class="form_item totalBlock">
            <p>优化建议</p>
            <el-input
              type="textarea"
              :rows="8"
              placeholder="请输入优化建议"
              v-model="addForm.suggest"
            ></el-input>
          </div>
          <div class="form_item">
            <p>Bug图片上传</p>
            <el-upload
              class="upload-demo"
              drag
              action="123"
              multiple
              :file-list="bugList"
              :on-change="beforeUploadBug"
              :on-remove="handleRemoveBug"
              :auto-upload="false"
              ref="uploadBug"
              accept="image/*"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                将文件拖到此处，或
                <em>点击上传</em>
              </div>
            </el-upload>
          </div>
          <div class="form_item">
            <p>预期完成时间</p>
            <el-date-picker
              value-format="yyyy-MM-dd"
              v-model="addForm.expect_time"
              type="date"
              placeholder="选择日期"
            >
            </el-date-picker>
          </div>
          <br />
          <div class="form_item">
            <el-button
              style="margin-left:20px"
              @click="submitAddForm"
              type="primary"
              >提交</el-button
            >
            <el-button @click="cancelAddForm" type="info">取消</el-button>
          </div>
        </div>
      </el-dialog>
      <el-dialog
        title="系统反馈修改"
        :visible.sync="editDialogVisible"
        width="80%"
        :before-close="cancelEditForm"
      >
        <div class="editForm">
          <div class="form_item">
            <p>项目名称</p>
            <el-input readonly v-model="editForm.project_name"></el-input>
          </div>
          <div class="form_item">
            <p>页面名称</p>
            <el-input readonly v-model="editForm.page_name"></el-input>
          </div>
          <div class="form_item totalBlock1">
            <p>系统问题</p>
            <el-input
              type="textarea"
              :rows="7"
              placeholder="请输入bug描述"
              v-model="editForm.description"
            ></el-input>
          </div>
          <div class="form_item totalBlock1">
            <p>优化建议</p>
            <el-input
              type="textarea"
              :rows="7"
              placeholder="请输入bug描述"
              v-model="editForm.suggest"
            ></el-input>
          </div>
          <div
            class="form_item"
            v-if="editImgList.length > 0"
            style="width:100%"
          >
            <p>图片列表</p>
            <div class="imgList">
              <!-- <img
                v-for="(imgSrc, CurIdx) in editImgList"
                :key="CurIdx"
                :src="'https://www.beyoung.group/static/' + imgSrc"
                alt=""
              /> -->
              <a
                target="_blank"
                v-for="(imgSrc, idx) in editImgList"
                :key="idx"
                :href="'https://www.beyoung.group/static/' + imgSrc"
                >查看原图</a
              >
            </div>
          </div>
          <div class="form_item">
            <p>Bug图片上传</p>
            <el-upload
              class="upload-demo"
              drag
              multiple
              action="123"
              :file-list="bugList2"
              :on-change="beforeUploadBugEdit"
              :on-remove="handleRemoveBugEdit"
              :auto-upload="false"
              ref="uploadBug"
              accept="image/*"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                将文件拖到此处，或
                <em>点击上传</em>
              </div>
            </el-upload>
          </div>
          <div class="form_item">
            <p>预期完成时间</p>
            <el-date-picker
              value-format="yyyy-MM-dd"
              v-model="editForm.expect_time"
              readonly
              type="date"
              placeholder="选择日期"
            >
            </el-date-picker>
          </div>
          <div
            class="form_item"
            style="    width: 100%;
    text-align: center;
    margin: 0 auto;"
          >
            <div class="btn">
              <el-button @click="submitEditForm" type="primary" size="mini"
                >提交</el-button
              >
              <el-button @click="cancelEditForm" type="info" size="mini"
                >取消</el-button
              >
            </div>
          </div>
        </div>
      </el-dialog>
      <el-dialog
        title="驳回详情"
        :visible.sync="turnDownFlag"
        width="400px"
        :before-close="closeTurnDown"
      >
        <!-- <img class="bugImg" :src="curImgSrc" alt="" /> -->
        <div>
          <p>
            <span>产品经理驳回：</span>
            {{ returnItem.cp_note }}
          </p>
          <p>
            <span>IT中间驳回：</span>
            {{ returnItem.zj_note }}
          </p>
          <div>
            <el-button type="primary" size="mini" @click="reDemand"
              >重新编辑</el-button
            >
          </div>
        </div>
      </el-dialog>
      <div class="des">
        <el-dialog
          title="系统问题描述"
          :visible.sync="bugDetail"
          width="400px"
          :before-close="closeBugDetail"
          :modal="false"
        >
          <el-form class="detailDialog" :model="bugForm">
            <el-form-item label="系统问题">
              <el-input
                v-model="bugForm.description"
                readonly
                type="textarea"
                :rows="3"
              ></el-input>
            </el-form-item>
            <el-form-item label="优化建议">
              <el-input
                v-model="bugForm.suggest"
                readonly
                type="textarea"
                :rows="3"
              ></el-input>
            </el-form-item>
            <el-form-item label="页面图片列表">
              <div>
                <!-- <img
                v-for="(imgSrc, idx) in bugForm.imglist"
                :key="idx"
                :src="imgSrc"
                alt=""
              /> -->
                <el-image
                  style="width: 200px;"
                  :src="imgSrc"
                  :preview-src-list="[imgSrc]"
                  v-for="(imgSrc, idx) in bugForm.imglist"
                  :key="idx"
                ></el-image>
              </div>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sys_names: [],
      bugForm: {
        description: "",
        suggest: "",
        imglist: []
      },
      bugDetail: false,
      searchVal: "",
      // curImgSrc: "",
      returnItem: "",
      turnDownFlag: false,
      sys_name: "运营系统",
      bugList: [], //bug上传文件列表
      page_names: [], // 获取的页面列表
      selectID: [], // 选中的表格行id
      // 新增form表单
      addFormData: "",
      addDialogVisible: false,
      addForm: {
        project_name: "",
        page_name: "",
        description: "",
        expect_time: "",
        initiator: "",
        suggest: "",
        need_type: ""
      },

      // 编辑弹框
      editDialogVisible: false,
      editFormData: "",
      bugList2: [], //bug上传文件列表
      editForm: {
        project_name: "",
        page_name: "xxx",
        description: "",
        suggest: "",
        expect_time: ""
      },
      editId: "",
      // 数据显示
      bugTable: [],
      turndown_reason: "",
      allPageList: {},
      username: "",
      editImgList: []
    };
  },
  methods: {
    formatTime() {
      let date = new Date();
      let year = date.getFullYear();
      let month =
        date.getMonth() + 1 > 10
          ? date.getMonth() + 1
          : "0" + (date.getMonth() + 1);
      let day =
        date.getDate() + 1 > 10
          ? date.getDate() + 1
          : "0" + (date.getDate() + 1);
      return year + "-" + month + "-" + day;
    },
    changePage() {
      console.log("切换页面");
      this.page_names = [];
      this.addForm.page_name = "";
      for (let key in this.allPageList) {
        if (key === this.addForm.project_name) {
          this.page_names = this.allPageList[key];
        }
      }
    },
    // bug详情
    showBugDetail(rowData) {
      console.log(rowData);
      console.log(111);
      this.bugForm.description = rowData.description;
      this.bugForm.suggest = rowData.suggest;
      let imgList = rowData.pic_url.split("@");
      for (let key in imgList) {
        imgList[key] = "https://www.beyoung.group/static/" + imgList[key];
      }
      this.bugForm.imglist = imgList;
      // this.bugForm.imglist = rowData.pic_url.split("@");
      this.bugDetail = true;
    },
    // 重新编辑需求
    reDemand() {
      // 打开系统编辑页面
      this.reEditRow(this.returnItem.id);
      this.turnDownFlag = false;
    },
    // 关闭bug详情
    closeBugDetail() {
      this.bugDetail = false;
    },
    // 显示bug详情
    showTurnDown(rowData) {
      this.returnItem = rowData;
      this.turnDownFlag = true;
      // this.turndown_reason = rowData;
    },
    // 搜索方法
    search() {
      // 请求

      this.getBugTable();
    },
    closeTurnDown() {
      this.turnDownFlag = false;
    },
    showBigImg(imgSrc) {
      this.showBigImgFlag = true;
      // this.curImgSrc = imgSrc;
    },
    // 图片类型检验
    checkImgType(filename) {
      return /\w(\.gif|\.jpeg|\.png|\.jpg|\.bmp)$/i.test(filename);
    },
    // 文件上传方法
    beforeUploadBug(files, fileList) {
      let file = files.raw;
      if (!this.checkImgType(file.name)) {
        this.bugList = [];
        this.$message.warning("请上传图片类型文件");
        return;
      }
      this.bugList = [];
      this.bugList.push(files);
    },

    handleRemoveBug(file, fileList) {
      this.bugList = fileList;
      console.log(file, fileList);
    },
    // 新增弹框
    submitAddForm() {
      if (this.bugList.length > 1) {
        alert("只能上传一个图片文件！");
        return;
      }
      this.addFormData = new FormData();
      for (let key in this.addForm) {
        console.log(this.addForm[key], key);
        if (key !== "need_type" && this.addForm[key] === "") {
          alert("除了图片文件外其他字段必须填写！");
          return;
        }

        this.addFormData.append(key, this.addForm[key]);
      }
      let curTime = this.formatTime();
      this.addFormData.append("initiate_time", curTime);
      console.log(this.bugList);
      for (let key in this.bugList) {
        this.addFormData.append("pic_url", this.bugList[key].raw);
      }

      this.addDialogVisible = false;
      // 发送请求
      let url = this._url2 + "server/personnel_management/bug_feedback/";
      this.$axios.post(url, this.addFormData).then(res => {
        console.log(res);
        if (res.data.code == 200) {
          alert("新增成功！");
          this.getBugTable();
          for (let key in this.addForm) {
            if (key === "initiator") continue;
            this.addForm[key] = "";
          }
          this.file = "";
          this.bugList = [];
        }
      });
    },
    cancelAddForm() {
      // 清空数据
      for (let key in this.addForm) {
        if (key === "initiator") continue;
        this.addForm[key] = "";
      }
      this.addDialogVisible = false;

      this.bugList = [];
    },
    // 编辑弹框
    beforeUploadBugEdit(files, fileList) {
      let file = files.raw;

      if (!this.checkImgType(file.name)) {
        this.bugList2 = [];
        this.$message.warning("请上传图片类型文件");
        return;
      }
      this.bugList2 = [];
      this.bugList2.push(files);
    },

    handleRemoveBugEdit(file, fileList) {
      this.bugList2 = fileList;
    },
    submitEditForm() {
      this.editFormData = new FormData();

      if (this.bugList2.length > 1) {
        alert("只能上传一个图片文件！");
        return;
      }

      for (let key in this.editForm) {
        if (!this.editForm[key]) {
          alert("所有字段必填！");
          return;
        }
        if (key === "description" || key === "suggest") {
          console.log(key);
          this.editFormData.append(key, this.editForm[key]);
        }
      }
      this.editDialogVisible = false;
      for (let key in this.bugList2) {
        this.editFormData.append("pic_url", this.bugList2[key].raw);
      }

      this.editFormData.append("id", this.editId);

      let url = this._url2 + "server/personnel_management/bug_feedback/";
      this.$axios.put(url, this.editFormData).then(res => {
        if (res.data.code == 200) {
          alert("修改成功");

          // 请求数据
          this.getBugTable();
          this.bugList2 = [];
          this.editId = "";
        } else {
          alert("修改未成功！");
        }
      });
    },
    cancelEditForm() {
      this.bugList2 = [];
      this.editDialogVisible = false;
    },
    // 顶部操作栏
    addBug() {
      this.addDialogVisible = true;
    },
    editBug() {
      this.selectID = [];
      let that = this;

      $(".tablebox input:checked").each(function() {
        console.log($(this));
        console.log("选中了", $(this).val());
        that.selectID.push($(this).val());
      });
      // 有延时？使用$nextTick
      if (!$(".tablebox input:checked").length) {
        alert("请选择一个进行编辑！");
        return;
      }
      if ($(".tablebox input:checked").length > 1) {
        alert("请选择一个进行编辑！");
        return;
      }

      // 赋值并弹框
      console.log(this.selectID);

      let curId = this.selectID[0];
      console.log(curId);
      let editItem = this.bugTable.find(i => i.id == curId);

      if (editItem.data_status !== "已提起") {
        alert("只能编辑待审核状态的数据！");
        return;
      }
      console.log(editItem);
      this.editForm.project_name = editItem["project_name"];
      this.editForm.page_name = editItem["page_name"];
      this.editForm.description = editItem["description"];
      this.editForm.suggest = editItem["suggest"];
      this.editId = curId;
      this.editForm.expect_time = editItem["expect_time"];
      this.editDialogVisible = true;
      if (editItem["pic_url"]) {
        this.editImgList = editItem["pic_url"].split("@");
      }
    },
    reEditRow(val) {
      this.addBug();
      let returnItem = this.bugTable.find(i => i.id == val);
      console.log(returnItem);
      for (let key in this.addForm) {
        this.addForm[key] = returnItem[key];
      }
    },
    deleteBug() {
      this.selectID = [];
      let that = this;
      $(".tablebox input:checked").each(function() {
        console.log("选中了", $(this).val());
        that.selectID.push($(this).val());
      });
      // 有延时？使用$nextTick
      if (
        !$(".tablebox input:checked").length ||
        $(".tablebox input:checked").length > 1
      ) {
        alert("请选择一个进行删除！");
        return;
      }
      let delectItem = this.bugTable.find(i => i.id == this.selectID[0]);
      if (delectItem.data_status !== "已提起") {
        alert("只能删除待审核状态的数据！");
        return;
      }
      console.log(this.selectID);
      //   单个删除还是多个删除
      this.$confirm("此操作将永久删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          // 删除
          // let data_id = JSON.stringify(this.selectID);
          let url = this._url2 + "server/personnel_management/bug_feedback/";
          this.$axios
            .delete(url, {
              params: {
                id: this.selectID[0]
              }
            })
            .then(res => {
              console.log(res);
              if (res.data.code === 200) {
                this.$message("删除成功！");
                this.getBugTable();
              }
            });
        })
        .catch(err => {
          console.log(err);
        });
    },
    // getBugTable() {
    //   let url = this._url2 + "server/personnel_management/select_bug_table/";
    //   this.$axios.get(url).then(res => {
    //     if (res.data.code == 200) {
    //       this.bugTable = res.data.data || [];
    //     } else {
    //       alert("获取数据失败");
    //     }
    //   });
    // },
    getBugTable() {
      let url = this._url2 + "server/personnel_management/bug_feedback/";
      this.$axios
        .get(url, {
          params: {
            condition: this.searchVal
          }
        })
        .then(res => {
          console.log(res);

          if (res.data.code == 200) {
            // this.page_names = res.data.data || [];
            this.allPageList = res.data.down_data;
            this.sys_names = Object.keys(this.allPageList) || [];
            this.bugTable = res.data.re_data || [];
          } else {
            alert("获取数据失败");
          }
        });
    }
  },
  created() {
    // this.getPageList();
    this.getBugTable();
    this.username = sessionStorage.getItem("user_name") || "";
    this.addForm.initiator = this.username;
  }
};
</script>
<style>
#bug_release .el-form-item__content {
  width: 220px;
}
#bug_release .el-dialog__body {
  padding-top: 0;
  padding-bottom: 15px;
}
#bug_release .el-upload-dragger {
  width: 264px;
}
#bug_release input[type="file"] {
  display: none;
}
#bug_release .el-form-item {
  margin-bottom: 0;
}
#bug_release .detailDialog textarea,
#bug_release .detailDialog .el-textarea__inner {
  width: 350px;
}
#bug_release textarea {
  width: 1088px;
}
</style>
<style scoped>
.des .el-dialog__wrapper  {
    z-index: 1000 !important;
}
#bug_release {
  width: 90%;
  text-align: center;
  margin: 20px auto;
}
.addForm,
.editForm {
  display: flex;
  flex-wrap: wrap;
}
.form_item {
  margin-bottom: 20px;
  display: flex;
  /* width: 420px; */
  align-items: center;
}
.form_item > p {
  text-align: left;
  text-indent: 3em;
  font-size: 16px;
  width: 200px;
}
.form_item > p + div {
  width: 263px;
  margin-left: 13px;
}
#bug_release table {
  border-collapse: collapse;
  font-size: 14px;
  margin: 20px auto;
}
